<template>
    <div class="p-20">
        <div class="filter-container" style="margin-bottom:20px" v-show="!dialogFormVisible">
            <el-input class="filter-item" style="width: 300px;" placeholder=""
                      v-model="listQuery.title">
            </el-input>
            <span></span>
            <el-select clearable style="width: 150px" class="filter-item" v-model="listQuery.categoryId"
                       placeholder="">
                <el-option v-for="status in allListOptions" :key="status.id" :label="status.name"
                           :value="status.id">
                </el-option>
            </el-select>
            <el-button class="filter-item" @click="handleFilter" size="small" style="margin-left: 30px">
                
            </el-button>
            <el-button style="margin-left: 10px;" type="success" size="small" @click="submit(false)">
            </el-button>
        </div>
        <div v-show="!dialogFormVisible">
            <el-table
                    ref="singleTable"
                    :data="tableData"
                    v-loading="listLoading"
                    element-loading-text=""
                    style="width: 100%; ">
                <el-table-column
                        v-for="item in tableHeader"
                        :type="item.type"
                        :key="item.prop"
                        :label="item.label"
                        :prop="item.prop"
                        :width="item.width"
                        :min-width="item.minWidth"
                        :align="item.align"
                        :formatter="item.formatter"
                        show-overflow-tooltip>
                </el-table-column>
                <el-table-column
                        fixed="right"
                        label=""
                        width="220">
                    <template scope="scope" class="filter-operation">
                        <ul style="list-style: none;height: 30px;">
                            <li style="width: 35px;float: left">
                                <el-button
                                        type="text"
                                        @click="deleteArticles(scope.row.id)">
                                </el-button>
                            </li>
                            <li style="width: 35px;float: left">
                                <el-button type="text"
                                           @click="submit(true,scope.row.id)">
                                </el-button>
                            </li>

                            <li style="width: 60px;float: left"
                                v-if="scope.row.status===2">
                                <el-button
                                        type="text"
                                        @click="enableDisable(3, scope.row.id,1,'')">
                                </el-button>
                            </li>
                            <li style="width: 60px;float: left"
                                v-else-if="scope.row.status===1">
                                <el-button
                                        type="text"
                                        @click="enableDisable(3, scope.row.id,2,'')">
                                </el-button>
                            </li>
                            <li style="width: 70px;float: left"
                                v-if="scope.row.isTop===null || scope.row.isTop===2">
                                <el-button
                                        type="text"
                                        @click="stickyPosts(2,scope.row.id,1, '')">
                                </el-button>
                            </li>
                            <li style="width: 60px;float: left"
                                v-else-if="scope.row.isTop===1">
                                <el-button
                                        type="text"
                                        @click="stickyPosts(2, scope.row.id,2,'')">
                                </el-button>
                            </li>

                        </ul>
                    </template>
                </el-table-column>
            </el-table>
            <div v-show="!listLoading" class="pagination">
                <el-pagination @size-change="userPageSize" @current-change="userPage"
                               :current-page="listQuery.page.page"
                               :page-sizes="[10,20,30, 50]" :page-size="listQuery.page.pageSize"
                               layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>







        <!-- <el-dialog title="" :visible.sync="dialogFormVisible">
            <el-form class="form-container" :model="postForm" :rules="rules" ref="postForm">
                <div class="createPost-main-container">
                    <el-row>
                        <el-col :span="21">
                            <el-form-item style="margin-bottom: 40px;" prop="title">
                                <MDinput name="name" v-model="postForm.title" required :maxlength="100">
                                    
                                </MDinput>
                                <span v-show="postForm.title.length>=26" class='title-prompt'>app</span>
                            </el-form-item>
                            <el-form-item style="margin-bottom: 40px;" prop="titleEn">
                                <MDinput name="name" v-model="postForm.titleEn" required :maxlength="100">
                                    
                                </MDinput>
                                <span v-show="postForm.titleEn.length>=26" class='title-prompt'>app</span>
                            </el-form-item>
                            <div class="postInfo-container">
                                <el-select clearable style="width: 150px" class="filter-item" placeholder=""
                                           v-model="postForm.categoryId">
                                    <el-option v-for="status in userLIstOptions" :key="status.id"
                                               :label="status.name"
                                               :value="status.id">
                                    </el-option>
                                </el-select>
                            </div>
                        </el-col>
                    </el-row>
                    <div class="editor-container">
                        <tinymce :height=400 ref="editor" v-model="postForm.content"></tinymce>
                    </div>

                    <div style="margin-bottom: 20px;">
                        <h1></h1>
                    </div>
                    <div class="editor-container">
                        <tinymce :height=400 ref="editor" v-model="postForm.contentEn"></tinymce>
                    </div>
                </div>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false"> </el-button>
                <el-button type="primary" @click="submitForm"> </el-button>
            </div>
        </el-dialog> -->








        <div class="m-addArticle" v-show="dialogFormVisible">


          <div class="m-addArticle-hd">
            <span :class="{ active : addArticleType == 1 }" @click=" addArticleType = 1 "></span>
            <span :class="{ active : addArticleType == 2 }" @click=" addArticleType = 2 "></span>
          </div>


          <span class="m-addArticle-label"></span>
          <el-radio-group v-model="postForm.categoryId">
            <el-radio :label="status.id" v-for="(status,index) in userLIstOptions " :key="index">{{ status.name }}</el-radio>
          </el-radio-group>

          <div class="m-addArticle-cn" v-show=" addArticleType == 1 ">
            <div class="m-addArticle-row">
              <span class="m-addArticle-label"></span>
              <el-input v-model="postForm.title"></el-input>
            </div>
            <div class="m-addArticle-row">
              <span class="m-addArticle-label"></span>
              <!-- <quill-editor v-model="postForm.content"
                ref="myQuillEditor"
                style="height:400px"
                :options="editorOption"
                @change="onEditorChange"
                @blur="onEditorBlur($event)"
                @focus="onEditorFocus($event)"
                @ready="onEditorReady($event)">
              </quill-editor> -->
              <my-editor :defaultMsg="defaultContent" :config="config" @content='editorValue'></my-editor>
            </div>
          </div>

          <div class="m-addArticle-en" v-show=" addArticleType == 2 ">
            <div class="m-addArticle-row">
              <span class="m-addArticle-label"></span>
              <el-input v-model="postForm.titleEn"></el-input>
            </div>
            <div class="m-addArticle-row">
              <span class="m-addArticle-label"></span>
              <!-- <quill-editor v-model="postForm.contentEn"
                style="height:400px"
                ref="myQuillEnEditor"
                :options="editorOption"
                @change="onEnEditorChange">
              </quill-editor> -->
              <my-editor :defaultMsg="defaultContentEn" :config="config" @content='editorValueEn'></my-editor>
            </div>
          </div>

          <div class="m-addArticle-ft">
            <el-button @click="dialogFormVisible=false"></el-button>
            <el-button type="primary"  @click="submitForm"></el-button>
          </div>

        </div>






    </div>
</template>
<script>
    // import ArticleDetail from './components/ArticleDetail'
    import DCTP from '@/utils/dctp';
    import Tinymce from '@/components/Tinymce'
    //import Upload from '@/components/Upload/singleImage3'
    import MDinput from '@/components/MDinput'
    import Multiselect from 'vue-multiselect'// element-uiselect
    import 'vue-multiselect/dist/vue-multiselect.min.css'// css
    import Sticky from '@/components/Sticky' // header
    import {validateURL} from '@/utils/validate'
    import editorImage from '@/components/Tinymce/components/editorImage'
    //import {fetchArticle} from '@/api/article'
    import MyEditor from '@/components/editor'
    import 'quill/dist/quill.core.css'
    import 'quill/dist/quill.snow.css'
    import 'quill/dist/quill.bubble.css'


    import {
      userSearch,
      submitform,
      getarticleall,
      fetchArticle,
      submitalter,
      deletearticles,
      enabledisable
    } from '@/api/article'

    import { quillEditor,Quill} from 'vue-quill-editor'
    import {container, ImageExtend, QuillWatch} from '@/assets/js/quill-image-extend-module'
    import ImageResize  from 'quill-image-resize-module'
    Quill.register( 'modules/ImageExtend', ImageExtend )
    Quill.register('modules/ImageResize', ImageResize )


    const listOptions = {id: 0, name: '', nameEn: ""};
    const defaultForm = {
        id: '',
        title: '', // 
        titleEn: '',   //
        content: '', // 
        contentEn: '', // 
        categoryId: '',//
        type: 2,//--2
        istop: 2,
        status: 2//--2
    };
    export default {
        name: 'articleDetail',
        components: {Tinymce, MDinput, Multiselect, Sticky , quillEditor, MyEditor },
        props: {
            isEdit: {
                type: Boolean,
                default: false
            }
        },
        data() {
            const validateRequire = (rule, value, callback) => {
                if (value === '') {
                    this.$message({
                        message: rule.field + '',
                        type: 'error'
                    })
                    callback(null)
                } else {
                    callback()
                }
            }
            const validateSourceUri = (rule, value, callback) => {
                if (value) {
                    if (validateURL(value)) {
                        callback()
                    } else {
                        this.$message({
                            message: 'url',
                            type: 'error'
                        })
                        callback(null)
                    }
                } else {
                    callback()
                }
            }
            return {
                 description: '', // 
                 config: {
                    // initialFrameWidth: 960,
                    initialFrameHeight: 650,
                    wordCount: false, // 
                    elementPathEnabled: false, // 
                    autoHeightEnabled: false, // 
                    enableAutoSave: false,
                    toolbars: [
                        [
                            'source', //
                            'customstyle', // 
                            'fontfamily', // 
                            'bold',
                            'fontsize', // 
                            'forecolor', // 
                            'backcolor', // 
                            '|',
                            'justifyleft', // 
                            'justifyright', // 
                            'justifycenter', // 
                            'justifyjustify', // 
                            '|',
                            'insertorderedlist', // 
                            'insertunorderedlist', // 
                            'indent', // 
                            'selectall', // 
                            'link', //
                            '|',
                            'inserttable', // 
                            'imagedes'
                        ]
                    ]
                },
                editorOption: {
                  // 
                  modules: {
                    ImageResize : { displaySize: true },
                    ImageExtend: {  // {}  base64
                      loading: false,
                      disableDropHandle: true,
                      name: 'uploadFile',  // 
                      size: 100,  //  M1M = 1024kb
                      action: '/upload/nofilter/file',  // , actionbase64
                      // response 
                      // {code: 200; data:{ url: 'baidu.com'}}
                      //  return res.data.url
                      response: (res) => {
                        return res.data
                      },
                      headers: (xhr) => {

                      },  //  
                      sizeError: () => {},  // 
                      start: () => {},  //  
                      end: () => {},  //  
                      error: () => {},  //  
                      success: () => {},  //   
                      change: (xhr, formData) => {
                        formData.append( 'type' , '1' )
                        formData.append( 'usageType' , '3' )
                      } //  headersformData
                    },
                    toolbar: { // 
                      container: [
                        ['bold', 'italic', 'underline', 'strike'],
                        [{'header': 1}, {'header': 2}],
                        [{'list': 'ordered'}, {'list': 'bullet'}],
                        [{'indent': '-1'}, {'indent': '+1'}],
                        [{'header': [1, 2, 3, 4, 5, 6, false]}],
                        [{'font': []}],
                        [{'color': [
                          'LightPink','Pink','Crimson','LavenderBlush','PaleVioletRed','HotPink','DeepPink','MediumVioletRed','Orchid','Thistle','plum','Violet','Magenta','Fuchsia','DarkMagenta','Purple','MediumOrchid','DarkVoilet','DarkOrchid','Indigo','BlueViolet','MediumPurple','MediumSlateBlue','SlateBlue','DarkSlateBlue','Lavender','GhostWhite','Blue','MediumBlue','MidnightBlue','DarkBlue','Navy','RoyalBlue','CornflowerBlue','LightSteelBlue','LightSlateGray','SlateGray','DoderBlue','AliceBlue','SteelBlue','LightSkyBlue','SkyBlue','DeepSkyBlue','LightBLue','PowDerBlue','CadetBlue','Azure','LightCyan','PaleTurquoise','Cyan','Aqua','DarkTurquoise','DarkSlateGray','DarkCyan','Teal','MediumTurquoise','LightSeaGreen','Turquoise','Auqamarin','MediumAquamarine','MediumSpringGreen','MintCream','SpringGreen','SeaGreen','Honeydew','LightGreen','PaleGreen','DarkSeaGreen','LimeGreen','Lime','ForestGreen','Green','DarkGreen','Chartreuse','LawnGreen','GreenYellow','OliveDrab','Beige','LightGoldenrodYellow','Ivory','LightYellow','Yellow','Olive','DarkKhaki','LemonChiffon','PaleGodenrod','Khaki','Gold','Cornislk','GoldEnrod','FloralWhite','OldLace','Wheat','Moccasin','Orange','PapayaWhip','BlanchedAlmond','NavajoWhite','AntiqueWhite','Tan','BrulyWood','Bisque','DarkOrange','Linen','Peru','PeachPuff','SandyBrown','Chocolate','SaddleBrown','SeaShell','Sienna','LightSalmon','Coral','OrangeRed','DarkSalmon','Tomato','MistyRose','Salmon','Snow','LightCoral','RosyBrown','IndianRed','Red','Brown','FireBrick','DarkRed','Maroon','White','WhiteSmoke','Gainsboro','LightGrey','Silver','DarkGray','Gray','DimGray','Black'
                        ]}, {'background': []}],
                        [{'align': []}],
                        ['image']
                      ],  // container
                      handlers: {
                        'image': function () {  // 
                          QuillWatch.emit(this.quill.id)
                        }
                      }
                    }
                  }
                },
                addArticleType: 1,  //1 2


                listOptions,
                allListOptions: [],
                dialogFormVisible: null,
                sublime: false,
                category: {
                    type: 2,
                    pageNum: 1,
                    pageSize: 100,
                },
                postForm: Object.assign({}, defaultForm),
                defaultContent: '',
                defaultContentEn: '',
                fetchSuccess: true,
                loading: false,
                userLIstOptions: [],
                platformsOptions: [
                    {key: 'a-platform', name: 'a-platform'},
                    {key: 'b-platform', name: 'b-platform'},
                    {key: 'c-platform', name: 'c-platform'}
                ],
                rules: {
                    image_uri: [{validator: validateRequire}],
                    title: [{validator: validateRequire}],
                    titleEn: [{validator: validateRequire}],
                    source_uri: [{validator: validateSourceUri, trigger: 'blur'}]
                },
                //
                total: null,
                //
                tableData: [],
                listQuery: {
                    categoryId: 0,
                    type: 2,
                    title: '',
                    page: {
                        page: 1,
                        pageSize: 10
                    }
                },
                tableHeader: [
                    {
                        label: "",
                        type: "index",
                        width: 65
                    }, {
                        label: "",
                        prop: "status",
                        width: 130,
                        align: 'center',
                        formatter(row, column, value) {
                            return value === 1 ? '' : (value === 2 ? '' : '');
                        }

                    }, {
                        label: "",
                        prop: "isTop",
                        width: 130,
                        align: 'center',
                        formatter(row, column, value) {
                            return value === 1 ? '' : (value === 2 ? '' : '');
                        }

                    }, {
                        label: "",
                        prop: "title",
                        width: 300
                    }, {
                        label: "",
                        prop: "titleEn",
                        width: 300
                    }, {
                        label: "",
                        prop: "category",
                        width: 150
                    }, {
                        label: "",
                        prop: "createTime",
                        width: 200,
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }, {
                        label: "",
                        prop: "createBy",
                    }
                ]
            }






        },
        computed: {
            tableDataLength() {
                return this.tableData.length;
            },
            editor() {
              return this.$refs.myQuillEditor.quill
            }
        },
        created() {

            if (this.isEdit) {
                this.fetchData()
            } else {
                this.postForm = Object.assign({}, defaultForm)
                this.defaultContentEn = ''
                this.defaultContent = ''
            }

        },
        methods: {
            /**
             *
             *@param {String}content
             */
            editorValue(content) {
                this.postForm.content = content
            },
            editorValueEn (content) {
                this.postForm.contentEn = content
            },


          onEditorBlur(quill) {
            // console.log('editor blur!', quill)
          },
          onEditorFocus(quill) {
            // console.log('editor focus!', quill)
          },
          onEditorReady(quill) {
            // console.log('editor ready!', quill)
          },
          onEditorChange({ quill, html, text }) {
            // console.log('editor change!', quill, html, text)
            // this.postForm.content = html
            // console.log( html )
          },
          onEnEditorChange({ quill, html, text }){
            // this.postForm.contentEn = html
            // console.log( html )
          },










            //
            submit(amend, num) {
                if (amend) {
                    this.fetchData(num);
                    this.sublime = true;
                } else {
                    this.postForm = Object.assign({}, defaultForm)
                    this.sublime = false;
                }
                this.dialogFormVisible = true;
            },
            //
            fetchData(num) {
                fetchArticle(num).then(response => {
                    if (response.result) {
                        this.postForm = response.data;
                        this.defaultContentEn = response.data.contentEn
                        this.defaultContent = response.data.content
                        console.log( this.postForm )
                        // tinyMCE.editors[0].setContent(this.postForm.content);
                        // tinyMCE.editors[1].setContent(this.postForm.contentEn);
                    } else {
                        this.msg = response.msg;
                        this.$message({
                            type: 'error',
                            message: this.msg
                        });
                    }
                })
                console.log(this.postForm);
            },
            //
            getList() {
                this.listLoading = true;
                getarticleall(this.listQuery).then(response => {
                    this.tableData = response.data.data;
                    this.total = response.data.page.count;
                    this.listLoading = false;
                })
            },
            /**
             * 
             */
            handleFilter() {
                this.getList();
            },
            //
            userPage(val) {
                this.listQuery.page.page = val;
                this.getList();
            },
            //
            userPageSize(val) {
                this.listQuery.page.pageSize = val;
                this.getList();
            },
            submitForm() {
                // console.log(this.sublime)
                // console.log("----------------------------------------------")
                // console.log(this.postForm)
                if (!this.sublime) {
                    submitform(this.postForm).then(response => {
                        if (response.result) {
                            this.loading = true
                            this.$notify({
                                title: '',
                                message: '',
                                type: 'success',
                                duration: 2000
                            })
                            this.dialogFormVisible = false
                            this.getList();
                        } else {
                            this.msg = response.msg;
                            this.$message({
                                type: 'error',
                                message: this.msg
                            });
                            return false
                        }
                    })
                } else {
                    submitalter(this.postForm).then(response => {
                        if (response.result) {
                            this.loading = true
                            this.$notify({
                                title: '',
                                message: '',
                                type: 'success',
                                duration: 2000
                            })
                            this.dialogFormVisible = false
                            this.getList();
                        } else {
                            this.msg = response.msg;
                            this.$message({
                                type: 'error',
                                message: this.msg
                            });
                            return false
                        }
                    })
                }
            },
            draftForm() {
                if (this.postForm.content.length === 0 || this.postForm.title.length === 0) {
                    this.$message({
                        message: '',
                        type: 'warning'
                    })
                    return
                }
                this.$message({
                    message: '',
                    type: 'success',
                    showClose: true,
                    duration: 1000
                })
                this.postForm.status = 'draft'
            },
            getRemoteUserList() {
                userSearch(this.category).then(response => {
                    this.allListOptions = response.data.data;
                    this.userLIstOptions = response.data.data;
                    this.allListOptions.unshift(this.listOptions);
                    if (!response.result) {
                        this.msg = response.msg;
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                    }
                })
            },
            deleteArticles(id) {
                deletearticles(id).then(response => {
                    if (!response.result) {
                        this.msg = response.msg;
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                    } else {
                        this.msg = '';
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                        this.getList();
                    }
                })
            },
            /**
             *
             * @param id
             * @param status
             * @param str
             */
            enableDisable(type, id, status, str) {
                enabledisable(type , id, status).then(response => {
                    if (response.result) {
                        this.getList();
                    }
                    this.getMessage(response.msg,response.result);
                })
            },
            /**
             *
             * @param id
             * @param status
             * @param str
             */
            stickyPosts(type ,id, status, str) {
                enabledisable(type , id, status).then(response => {
                    if (response.result) {
                        this.getList();
                    }
                    this.getMessage(response.msg,response.result);
                })
            }
        },
        created() {
            this.getRemoteUserList();
            this.getList();
        },
        mounted() {

          // this.editor = UE.getEditor('editor')



        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    @import "src/styles/mixin.scss";

    .title-prompt {
        position: absolute;
        right: 0px;
        font-size: 12px;
        top: 10px;
        color: #ff4949;
    }

    .createPost-container {
        position: relative;
        .createPost-main-container {
            padding: 40px 45px 20px 50px;
            .postInfo-container {
                position: relative;
                @include clearfix;
                margin-bottom: 10px;
                .postInfo-container-item {
                    float: left;
                }
            }
            .editor-container {
                min-height: 500px;
                margin: 0 0 30px;
                .editor-upload-btn-container {
                    text-align: right;
                    margin-right: 10px;
                    .editor-upload-btn {
                        display: inline-block;
                    }
                }
            }
        }
        .word-counter {
            width: 40px;
            position: absolute;
            right: -10px;
            top: 0px;
        }
    }





    .m-addArticle{
      max-width: 1000px;
      .m-addArticle-hd{
        margin-bottom: 30px;
        font-size: 14px;
        border-bottom: 1px solid #e4e7ed;
        span{
          display: inline-block;
          line-height: 40px;
          padding-right: 30px;
          transition: all .3;
          &.active{
            font-weight: bold;
            color: #409eff;
          }
          &:hover{
            cursor: pointer;
            color: #409eff;
          }
        }
      }
      .m-addArticle-ft{
        margin-top: 70px;
        text-align: right;
      }
      .m-addArticle-label{
        display: block;
        color: #ccc;
        margin-bottom: 10px;
      }
      .el-radio-group,
      .el-input{
        margin-bottom: 40px;
      }
    }


</style>



