<template>
  <div>
    <script :id="editorId" type="text/plain"></script>
    <el-dialog :visible.sync="toUpLoad">
      <div class="">
        <el-upload class="upload-demo" drag action="" :http-request="upLoadImg">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">
            <em></em>
          </div>
          <div class="el-upload__tip" slot="tip">jpg/png10M</div>
        </el-upload>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="toUpLoad = false, imgs = []"> </el-button>
        <el-button type="primary" @click="toUpLoad = false, recieveImg()"> </el-button>
      </div>
    </el-dialog>
    <!-- <img-store ref="upload" :bar="false" @checkedImgs="recieveImg"></img-store> -->
  </div>
</template>
<script>
  // import '../../../static/UE/ueditor.config.js'
  // import '../../../static/UE/ueditor.all.js'
  // import '../../../static/UE/lang/zh-cn/zh-cn.js'
  // import '../../../static/UE/ueditor.parse.min.js'
  // import fetch from '@/utils/fetch';
  import axios from 'axios'
  export default {
    name: 'MyEditor',
    components: {
      //   ImgStore
    },
    data() {
      return {
        editorId: 'editorId_' + Math.random() * Date.now(), // id 
        editor: null,
        imgs: [],
        toUpLoad: false,

        config: {
          // initialFrameWidth: 960,
          initialFrameHeight: 650,
          wordCount: false, // 
          // elementPathEnabled: false, // 
          autoHeightEnabled: false, // 
          enableAutoSave: false,
          toolbars: [
            [
              'anchor', //
              'undo', //
              'redo', //
              'bold', //
              'indent', //
              'snapscreen', //
              'italic', //
              'underline', //
              'strikethrough', //
              'subscript', //
              'fontborder', //
              'superscript', //
              'formatmatch', //
              'source', //
              'blockquote', //
              'pasteplain', //
              'selectall', //
              'print', //
              'preview', //
              'horizontal', //
              'removeformat', //
              'time', //
              'date', //
              'unlink', //
              'insertrow', //
              'insertcol', //
              'mergeright', //
              'mergedown', //
              'deleterow', //
              'deletecol', //
              'splittorows', //
              'splittocols', //
              'splittocells', //
              'deletecaption', //
              'inserttitle', //
              'mergecells', //
              'deletetable', //
              'cleardoc', //
              'insertparagraphbeforetable', //""
              'insertcode', //
              'fontfamily', //
              'fontsize', //
              'paragraph', //
              'imagedes', // 
              'edittable', //
              'edittd', //
              'link', //
              'emotion', //
              'spechars', //
              'searchreplace', //
              'map', //Baidu
              'gmap', //Google
              'help', //
              'justifyleft', //
              'justifyright', //
              'justifycenter', //
              'justifyjustify', //
              'forecolor', //
              'backcolor', //
              'insertorderedlist', //
              'insertunorderedlist', //
              'fullscreen', //
              'directionalityltr', //
              'directionalityrtl', //
              'rowspacingtop', //
              'rowspacingbottom', //
              'pagebreak', //
              'insertframe', //Iframe
              'imagenone', //
              'imageleft', //
              'imageright', //
              'imagecenter', //
              'wordimage', //
              'lineheight', //
              'edittip ', //
              'customstyle', //
              'autotypeset', //
              'touppercase', //
              'tolowercase', //
              'background', //
              'template', //
              'inserttable', //
            ]
          ]
        }
      }
    },
    props: {
      defaultMsg: {
        type: String
      },
      // config: {
      //   type: Object
      // }
    },
    watch: {
      'defaultMsg' (now, old) {
        let that = this;
        if (now) {
          setTimeout(() => {
            that.editor.setContent(now)
          }, 600)
        }
      }
    },
    mounted() {
      const _this = this
      // 
      window.UE.plugin.register('imagedes', function () {
        return {
          commands: {
            'imagedes': {
              execCommand: function () {
                // 
                _this.toUpLoad = true;
                _this.imgs = [];
                // _this.$refs.upload.toCheckPic()
              }
            }
          }
        }
      })
      this.editor = window.UE.getEditor(this.editorId, this.config) // UE

      this.editor.ready = function () {
        var msg = _this.defaultMsg
        _this.editor.setContent(msg) // UE
      }
      this.editor.addListener('blur', function () {
        var content = _this.editor.getContent()
        _this.$emit('content', content)
      })
      // setTimeout( () => {
      //   this.editor.setContent(this.defaultMsg)
      // },2000)
    },
    methods: {
      // 
      upLoadImg(param) {
        const fileObj = param.file;
        // 
        const URL = '/upload/nofilter/file'
        // FormData 
        var param = new FormData();
        // 
        param.append("uploadFile", fileObj);
        param.append('type', '1')
        param.append('usageType', '3')
        // 
        let config = {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }
        let that = this;
        // 
        axios.post(URL, param, config)
          .then(response => {
            if (response.data.result) {
              that.imgs.push({
                imgPath: response.data.data
              })
            } else {
              that.$message.error(response.data.msg || '')
            }
          })
      },
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
        console.log()
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 10;

        if (!isJPG) {
          this.$message.error(' JPG !');
        }
        if (!isLt2M) {
          this.$message.error(' 10MB!');
        }
        return isJPG && isLt2M;
      },

      getUEContent() { // 
        return this.editor.getContent()
      },
      // 
      recieveImg() {
        let that = this
        this.imgs.map(item => {
          that.editor.execCommand('insertimage', [{
            src: item.imgPath
          }])
        })

      },
      destroyed() {
        this.editor.destroy();
      }

    }

  }
</script>
<style>
  .edui-for-imagedes .edui-icon {
    background-image: url('upload-other.png') !important;
    background-repeat: no-repeat;
    background-position: 2px 5px;
  }

  .el-upload {
    width: 100%;
  }

  .el-upload-dragger {
    width: 100% !important;
  }
</style>


